import WaterBoundarySetting from "../Data/WaterBoundarySetting";
import LetterGenerator from "./LetterGenerator";

const {ccclass, property} = cc._decorator;

@ccclass
export default class LetterHubGenerator extends cc.Component {

    @property(cc.Prefab)
    letterPrefab: cc.Prefab = null;

    @property(WaterBoundarySetting)
    waterBoundarySetting: WaterBoundarySetting = null;

    protected onLoad(): void {
        const boundaryRect = this.waterBoundarySetting.boundaryRect;
        const letterWidth = 200; // Example width of the letter prefab
        const letterHeight = 200; // Example height of the letter prefab
        const letterGenerator = new LetterGenerator(this.letterPrefab, boundaryRect, letterWidth, letterHeight);

        // Generate letters for the word "APPLE"
        letterGenerator.generateLetters('APPLE');
    }
}
